/* NAVBAR */
#navbar{
  width: 100%;
  /* flex-direction: row; */
  height: 10%;
  
}
#navbar #title{
  font-size: 70px;
  text-align: left;
  width: 20%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

#navbar div{
  float: right;
  margin-right: 5%;
  text-align: right;
  display: inline-block;
  margin-top: 30px;
}
#navbar a{
  font-size: 30px;
  margin-right: 5%;
  color: grey;
}

#navbar a:active{
  color: lavender
}
